.size-pizza {
	padding: 8%;
	width: 100%;
	height: 40%;
	text-align: center;
	background-color: white;
	border-radius: 10px;
	opacity: 0.7;
}
.size-pizza-title {
	margin-bottom: 10px;
	font-size: 35px;
	font-family: 'Merienda', Courier, monospace;
}
.size-pizza-img img {
	margin: 0 20px;

	border-radius: 50%;
	cursor: pointer;
}
.small-size {
	width: 15%;
}
.midt-size {
	width: 20%;
}
.big-size {
	width: 25%;
}

.base-choise {
	border: 2px solid red;
}
